import { Injectable, EventEmitter } from '@angular/core';
import { HallType } from './hall-type';
import { BehaviorSubject, of, Subject, Observable, AsyncSubject, ReplaySubject } from 'rxjs';
import { tap, single, map, find, filter, first, pluck, take } from 'rxjs/operators';
import { HttpClient, HttpHeaders, HttpParams } from '@angular/common/http';

@Injectable()
export class HallsDataService {

    private costumers: any = new AsyncSubject();
    private costumer: any = new BehaviorSubject(1);

    public costumerObsever = this.costumer.asObservable();
    // public costumersResolver = this.costumers.asObservable();

    private galleries: any;
    private costs: any;

    constructor(private http: HttpClient) {

        /* this.costumers = this.http.get("http://ethio:8080/costumers")
            .pipe(
                tap(res => {
                    this.galleries = of(res['galleries']);
                    this.videos = of(res['videos']);
                }),
            ); */
            this.costumers = this.http.get("http://ethio:8080/costumers")
            .pipe(
                tap(res => {
                    this.costs = (res['costumers']);
                    this.galleries = res['galleries'];
                }),
                
            );
    }

    public getCostumers() {
        let costumers = new Promise((resolve,reject) => {
        if(this.costs){ return resolve(this.costs);}

            let co = this.costumers.pipe(map(data => data["costumers"]));
            return co? resolve(co):reject("No data!");
            
        });
        return this.costumers;
    }

    costumerEmit(costumer) {
        this.costumer.next(costumer);
    }

    getGallery(id) {
        //return this.findItems(this.galleries, id);
        //if(this.costs){ return this.costs.filter((items) => {return items.costumer_id == id;});}

        return this.galleries.find((items) => {return items.costumer_id == id;});
    }

    getVideo(id) {
        // return this.videos.filter((items) => {return items.costumer_id == id;});
        // return this.findItems(this.videos, id);
    }

    storeData(postUrl, inputData, opt) {
        return this.http.post(postUrl, inputData, opt);
    }

    patchData(postUrl, inputData, opt) {
        return this.http.patch(postUrl, inputData, opt);
    }

    putData(postUrl, inputData, opt) {
        return this.http.put(postUrl, inputData, opt);
    }

    public getById(costumerName: any) {
        
        let prop: any = "company";
        if (isFinite(costumerName)) {
            prop = "id";
            costumerName = +costumerName;
        }
        if(this.costs){
            let costumer = this.costs.find((items) => {return items[prop] == costumerName;});
            (! costumer)? this.costumer.next({}): this.costumer.next(costumer);
            return this.costumerObsever;
        } 
        
        this.costumers.
            subscribe(
                (data) => {
                    let res = data['costumers'].find((elem) => { return elem[prop] == costumerName });//this.finCostumer(elem,prop,costumerName)
                    
                    (! res)? this.costumer.next({}): this.costumer.next(res);
                    if(! res) console.log("No recourse url!");
                    return res;
                    
                }
            );
        return this.costumerObsever;
    }

    finCostumer(elem,prop,costName){
        return elem[prop] == costName;
    }

    public getCostumer(param?) {
        return ((this.costumer.value === 1)) ? this.getById(param) : this.costumerObsever;
    }

    findItems(obj, id) {

        let arrItem = [];
        let gals = obj['value'];
        let idx = 0;
        for (let ii of gals) {

            if (ii.costumer_id === id) {
                //this.costumer.next(gals[ii]);
                arrItem[idx++] = ii;
            }
        }
        return arrItem;
    }

   
}

