import { Injectable, Inject } from '@angular/core';
import { Resolve, Router } from '@angular/router';
import { HallType } from '../../../costumers/hall-type';
import { Observable } from 'rxjs';
import { HallsDataService } from '../../../costumers/halls-data-service';
import { map } from 'rxjs/operators';

@Injectable()

export class HallResolver implements Resolve<HallType> {

    constructor(private http: HallsDataService,private router:Router){}

    resolve(): Observable<HallType[]> | Promise<HallType[]> | HallType[] | any {
        return this.http.getCostumers();
    }
}